:host {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
}

.erp-left-menu {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 280px;
  z-index: 1002;
  margin: 0 !important;
  padding: 0 !important;
  background: var(--bg-primary);
  border-right: 1px solid var(--border-color);
  box-shadow: 2px 0 8px var(--shadow);
  animation: slideInFromLeft 0.3s ease-out;

  .main-menu,
  .expanded-panel {
    height: 100%;
    overflow-y: auto;
  }

  .panel-header {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px 20px;
    border-bottom: 1px solid var(--border-color);
    background: var(--bg-secondary);
    position: sticky;
    top: 0;
    z-index: 10;

    h3 {
      flex: 1;
      margin: 0;
      font-size: 18px;
      font-weight: 600;
      color: var(--text-primary);
    }

    .back-btn,
    .close-btn {
      background: none;
      border: none;
      font-size: 28px;
      cursor: pointer;
      color: var(--text-muted);
      padding: 0;
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.2s ease;
    }

    .back-btn:hover {
      color: var(--accent-primary);
      transform: translateX(2px);
    }

    .close-btn:hover {
      color: var(--accent-danger);
      transform: rotate(90deg);
    }
  }

  .category-list {
    padding: 12px;

    .category-item {
      display: flex;
      align-items: center;
      gap: 16px;
      padding: 16px;
      margin-bottom: 8px;
      background: var(--bg-secondary);
      border-radius: 12px;
      cursor: pointer;
      transition: all 0.2s ease;
      border: 1px solid transparent;

      &:hover {
        background: var(--bg-tertiary);
        border-color: var(--primary-color);
        transform: translateX(4px);
        box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
      }

      .category-icon {
        font-size: 32px;
        width: 48px;
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--bg-tertiary);
        border-radius: 10px;
      }

      .category-content {
        flex: 1;

        .category-title {
          font-size: 15px;
          font-weight: 600;
          color: var(--text-primary);
          margin-bottom: 4px;
        }

        .category-count {
          font-size: 12px;
          color: var(--text-secondary);
          opacity: 0.7;
        }
      }

      .category-arrow {
        font-size: 24px;
        color: var(--text-secondary);
        opacity: 0.5;
        transition: all 0.2s ease;
      }

      &:hover .category-arrow {
        opacity: 1;
        transform: translateX(4px);
      }
    }
  }

  .panel-items {
    padding: 12px;

    .menu-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 14px 16px;
      margin-bottom: 8px;
      background: var(--bg-secondary);
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s ease;
      border: 1px solid transparent;

      &:hover {
        background: var(--bg-tertiary);
        border-color: var(--primary-color);
        transform: translateX(4px);
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
      }

      .item-content {
        flex: 1;

        .item-label {
          font-size: 14px;
          font-weight: 600;
          color: var(--text-primary);
          margin-bottom: 4px;
        }

        .item-description {
          font-size: 12px;
          color: var(--text-secondary);
          opacity: 0.8;
        }
      }

      .item-arrow {
        font-size: 20px;
        color: var(--text-secondary);
        opacity: 0.5;
        transition: all 0.2s ease;
      }

      &:hover .item-arrow {
        opacity: 1;
        transform: translateX(4px);
      }
    }
  }

  @keyframes slideInFromLeft {
    from {
      transform: translateX(-100%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }
}

// 반응형 디자인
@media (max-width: 992px) {
  .erp-left-menu {
    width: 280px;
    box-shadow: 4px 0 12px var(--shadow);
  }
}

@media (max-width: 768px) {
  .erp-left-menu {
    width: 80%;
    max-width: 280px;

    .panel-header {
      padding: 12px 16px;

      h3 {
        font-size: 16px;
      }

      .back-btn,
      .close-btn {
        width: 28px;
        height: 28px;
        font-size: 24px;
      }
    }

    .category-list {
      padding: 8px;

      .category-item {
        padding: 12px;
        gap: 12px;

        .category-icon {
          font-size: 28px;
          width: 40px;
          height: 40px;
        }

        .category-content {
          .category-title {
            font-size: 14px;
          }

          .category-count {
            font-size: 11px;
          }
        }

        .category-arrow {
          font-size: 20px;
        }
      }
    }

    .panel-items {
      padding: 8px;

      .menu-item {
        padding: 12px;

        .item-content {
          .item-label {
            font-size: 13px;
          }

          .item-description {
            font-size: 11px;
          }
        }

        .item-arrow {
          font-size: 18px;
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .erp-left-menu {
    width: 85%;

    .category-list {
      .category-item {
        padding: 10px;
        gap: 10px;

        .category-icon {
          font-size: 24px;
          width: 36px;
          height: 36px;
        }

        .category-content {
          .category-title {
            font-size: 13px;
          }
        }
      }
    }
  }
}
